@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato/latoregular.woff2") format("woff2"), url("../fonts/lato/latoregular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  display: flex;
  height: 100%;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@media (min-width: 1440px) {
  .header {
    position: relative;
  }
}

@media (min-width: 1440px) {
  .header--home::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 50%;
    z-index: -1;
    margin-bottom: -11px;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url(../img/cat-green-desktop.jpg);
    background-repeat: no-repeat;
    background-position: left;
    background-size: cover;
  }
}

.header__nav {
  padding: 13px 0;
}
@media (min-width: 768px) {
  .header__nav {
    padding-top: 25px;
  }
}
@media (min-width: 1440px) {
  .header__nav {
    padding-top: 57px;
  }
}

.title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}

.title-lines {
  position: relative;
  margin: 0 -20px;
  overflow: hidden;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  font-family: "Oswald", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .title-lines {
    margin: 0 -30px;
    padding-left: 30px;
    font-size: 36px;
    line-height: 46px;
  }
}
@media (min-width: 1440px) {
  .title-lines {
    margin: 0 -102px 0 -110px;
    padding-left: 70px;
  }
}
.title-lines::after, .title-lines::before {
  content: "";
  position: absolute;
  top: 18px;
  display: inline-block;
  width: 100%;
  height: 2px;
  vertical-align: middle;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .title-lines::after, .title-lines::before {
    top: 25px;
  }
}
.title-lines:before {
  display: none;
  margin-left: -100%;
}
@media (min-width: 768px) {
  .title-lines:before {
    display: inline-block;
  }
}
.title-lines:after {
  margin-right: -100%;
}

.title-lines span {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .title-lines span {
    padding: 0 37px;
  }
}

.wrapper {
  width: 100%;
  min-width: 320px;
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .wrapper {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    padding: 0;
  }
}

.button {
  display: block;
  width: 168px;
  margin: 0;
  padding: 10px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}

.nav {
  position: relative;
  display: grid;
  justify-content: space-between;
  align-items: center;
  min-height: 65px;
  grid-template-columns: repeat(2, auto);
}
@media (min-width: 768px) {
  .nav {
    padding: 24px 0 17px;
    gap: 20px;
    grid-template-columns: auto 1fr;
  }
}
@media (min-width: 1440px) {
  .nav {
    padding: 55px 0 20px;
    grid-template-columns: repeat(2, 1fr);
  }
}

.nav__logo {
  display: block;
  width: 191px;
  height: 38px;
}
@media (min-width: 768px) {
  .nav__logo {
    width: 174px;
    height: 50px;
  }
}
@media (min-width: 1440px) {
  .nav__logo {
    width: 202px;
    height: 59px;
  }
}

.nav__logo img {
  display: block;
  width: 100%;
  height: 100%;
}

.nav__toggle {
  position: relative;
  width: 30px;
  height: 30px;
  margin-right: -2px;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav__toggle-line {
  position: absolute;
  top: 46%;
  left: 50%;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: translate(-50%, -50%);
}
.nav__toggle-line::before, .nav__toggle-line::after {
  content: "";
  position: absolute;
  left: 50%;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: translateX(-50%);
}
.nav__toggle-line::before {
  top: 8px;
}
.nav__toggle-line::after {
  top: -8px;
}

.nav.is-open .nav__toggle-line {
  background-color: transparent;
}
.nav.is-open .nav__toggle-line::before {
  top: 50%;
  transform: translate(-35%, -50%) rotate(-46deg);
}
.nav.is-open .nav__toggle-line::after {
  top: 50%;
  transform: translate(-35%, -50%) rotate(46deg);
}

.nav__list {
  position: absolute;
  top: 100%;
  right: -20px;
  left: -20px;
  z-index: 2;
  display: none;
  flex-direction: column;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .nav__list {
    position: static;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-right: 5px;
    background-color: transparent;
    gap: 33px;
  }
}
@media (min-width: 1440px) {
  .nav__list {
    margin-right: 0;
    gap: 34px;
  }
}

.nav.is-open .nav__list {
  display: flex;
}

.nav__item {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .nav__item {
    border: none;
  }
}

.nav__link {
  display: block;
  padding-top: 23px;
  padding-bottom: 22px;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .nav__link {
    padding: 10px 0;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .nav__link {
    padding: 6px 0;
    font-size: 20px;
    line-height: 30px;
  }
}

@media (min-width: 768px) {
  .nav__link--current {
    text-decoration: 2px;
    outline: none;
    text-decoration-color: #68b738;
    text-decoration-line: underline;
    text-decoration-style: solid;
    text-underline-offset: 12px;
  }
}

@media (min-width: 1440px) {
  .header--home .nav__link {
    color: #ffffff;
  }
}

@media (min-width: 1440px) {
  .header--home .nav__link--current {
    text-decoration-color: inherit;
  }
}

@media (max-width: 767px) {
  .no-js .nav {
    padding-bottom: 0;
  }
}

@media (max-width: 767px) {
  .no-js .nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .no-js .nav__list {
    position: static;
    display: block;
    margin: 13px -20px 0;
    grid-column: 1/-1;
  }
}

