@media (min-width: 768px) {
  .control__input--check:checked {
    background-size: 24px 19px;
  }
}

.footer {
  background-color: #f2f2f2;
}

.footer__links {
  padding: 40px 0;
}
@media (min-width: 768px) {
  .footer__links {
    padding: 56px 0;
  }
}
@media (min-width: 1440px) {
  .footer__links {
    padding: 67px 0;
  }
}

@media (min-width: 768px) {
  .footer__wrapper {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 140px 1fr;
  }
}

.footer__logo {
  display: block;
  width: 128px;
  height: 24px;
  margin: 0 auto 19px;
  color: #111111;
}
@media (min-width: 768px) {
  .footer__logo {
    width: 128px;
    height: 24px;
    margin: 0;
  }
}
.footer__logo:hover, .footer__logo:focus {
  color: #3e3e3e;
}
.footer__logo:active {
  color: #6b6b6b;
}

.footer__logo svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: currentColor;
}

.footer__social {
  width: 100%;
  margin-bottom: 20px;
  padding-top: 20px;
  padding-bottom: 19px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .footer__social {
    margin: 0 auto;
    padding: 0;
    border: none;
  }
}

.social {
  display: grid;
  justify-content: center;
  gap: 24px;
  grid-template-columns: repeat(3, auto);
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 22px;
  color: #666666;
  fill: currentColor;
}
.social__link:hover, .social__link:focus {
  color: #68b738;
  outline: none;
}
.social__link:active {
  color: rgba(105, 183, 56, 0.3);
}

.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  line-height: 20px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
  text-decoration: none;
  gap: 22px;
}
@media (min-width: 768px) {
  .copyright {
    margin-left: auto;
  }
}
.copyright:focus {
  outline: none;
}
.copyright:active {
  opacity: 0.3;
}

.copyright svg {
  width: 27px;
  height: 34px;
  fill: #666666;
}

.copyright:hover svg,
.copyright:focus svg {
  fill: #68b738;
}

.location--home {
  background-color: #ffffff;
}

.location__wrapper {
  position: relative;
}

.location__contact {
  display: grid;
  padding: 26px 0 27px;
  grid-template-columns: 1fr 1fr;
}
@media (min-width: 768px) {
  .location__contact {
    padding: 59px 39px 62px;
    gap: 10px 250px;
  }
}
@media (min-width: 1440px) {
  .location__contact {
    position: absolute;
    top: 94px;
    left: 0;
    width: 565px;
    padding: 60px 80px;
    background-color: #ffffff;
    gap: 10px 90px;
  }
}

.location__title {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #111111;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .location__title {
    font-size: 20px;
    line-height: 26px;
  }
}

.location__address {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}
@media (min-width: 768px) {
  .location__address {
    font-size: 16px;
    text-align: right;
  }
}
@media (min-width: 1440px) {
  .location__address {
    text-align: left;
  }
}

@media (min-width: 768px) {
  .location__address span {
    display: inline-block;
    margin-top: 18px;
  }
}

.location__iframe-wrapper {
  width: 100%;
  height: 362px;
}
@media (min-width: 768px) {
  .location__iframe-wrapper {
    height: 400px;
  }
}

.location__iframe-wrapper img {
  display: block;
  width: 100%;
  height: 100%;
}

.location__iframe {
  height: 100%;
}

.location__iframe iframe {
  display: block;
  width: 100%;
  height: 100%;
  border: none;
}

.no-js .location__iframe {
  display: none;
}